.container{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}
.imgCover{
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 20px;
    margin-bottom: 5px;
}
.title{
    border: 5px 0 1px 0;
}
.description{
    font-size: 15px;
    position: relative;
    max-height: 60px;
    overflow: hidden;
    padding-right: 10px;
    margin-bottom: 15px;
}
.description::before{
    content: "...";
    bottom: 0;
    right: 0;
}

.container footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
}
.link{
    color: black;
    text-decoration: none;
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px;
}
.author{
    display: flex;
    align-items: center;
}
.authorAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 5px;
}
.authorName{
    font-size: 20px;
    font-weight: 500;
}
.createdAt{
    font-size: 10px;
    color: grey;
    font-weight: 500;
}