.container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 10px;
}
.inner{
    background-color: whitesmoke;
    /* width: 100px; */
    height: 30px;
    padding: 2px 15px;
    border-radius: 5px;
    border: none;
    /* display: flex;
    justify-content: center;
    align-items: center; */
}
.input{
    border: none;
    background-color:  whitesmoke;
    line-height: 30px;
}
.input:focus{
    outline: none;
}
.btn{
    width: 40px;
    height: 25px;
    color: white;
    background-color: blue;
    margin-left: 5px;
    border: none;
    border-radius: 4px;
}
.spn{
    cursor: pointer;
    font-weight: bold;
}